.general-info {
  margin: 20px auto;
  padding: 15px;
  width: 80%;
  background-color: #ffffff;
  border-radius: 8px;
  box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.1);
}

.general-info h2 {
  color: #333;
  margin-bottom: 15px;
  font-size: 20px;
}

.general-info input {
  width: 90%;
  padding: 10px;
  margin: 5px 0;
  border: 1px solid #ccc;
  border-radius: 4px;
  font-size: 14px;
}

.general-info button {
  padding: 10px 20px;
  margin-top: 10px;
  background-color: #007bff;
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  font-size: 14px;
}

.general-info button:hover {
  background-color: #0056b3;
}
