.cv-template {
    margin: 20px auto;
    padding: 30px;
    width: 80%;
    background-color: #ffffff;
    border-radius: 10px;
    box-shadow: 0px 4px 12px rgba(0, 0, 0, 0.1);
    font-family: 'Arial', sans-serif;
    line-height: 1.6;
    color: #333;
  }
  
  .cv-template h2 {
    margin-bottom: 15px;
    font-size: 22px;
    color: #007bff;
    text-align: left;
    border-bottom: 2px solid #007bff;
    padding-bottom: 5px;
  }
  
  .cv-template h3 {
    margin: 10px 0;
    font-size: 18px;
    color: #555;
  }
  
  .cv-template p {
    margin: 5px 0;
    font-size: 16px;
  }
  
  .cv-template .section {
    margin-bottom: 20px;
  }
  
  .cv-template .section:last-child {
    margin-bottom: 0;
  }
  
  .cv-template .info-block {
    margin-bottom: 15px;
    padding-left: 10px;
    border-left: 3px solid #007bff;
  }
  
  .cv-template .info-block h3 {
    margin-bottom: 8px;
    font-size: 18px;
    color: #007bff;
  }
  
  .cv-template .info-block p {
    margin: 3px 0;
    font-size: 14px;
    color: #555;
  }
  
  .cv-template .dates {
    font-size: 14px;
    color: #888;
    font-style: italic;
  }
  
  .cv-template .highlight {
    color: #007bff;
    font-weight: bold;
  }
  